import { Component, OnInit } from '@angular/core';
import { PopoverController } from '@ionic/angular';
import { PeriodPickerPage } from '../period-picker/period-picker.page';
import { DatabaseService } from 'src/app/api/database.service';
import { UserService } from '../api/user.service';
@Component({
  selector: 'app-werbung',
  templateUrl: './werbung.component.html',
  styleUrls: ['./werbung.component.scss'],
})
export class WerbungComponent implements OnInit {

  constructor(
    private PopoverController: PopoverController,
    public DatabaseService: DatabaseService,
    public UserService: UserService
    ) { }

  ngOnInit() {}

  async scheduleTime(ev: any) {
    const popover = await this.PopoverController.create({
      component: PeriodPickerPage,
      cssClass: 'set-time-class',
      event: ev,
      translucent: true
    });
    return await popover.present();
  }

  async createAdvertise() {
    this.UserService.page = 'create-advertisement';
  }

}
