import { Component, OnInit } from '@angular/core';
import { UserService } from '../api/user.service';
import { NavigationExtras, Router } from '@angular/router';


@Component({
  selector: 'app-bookingList',
  templateUrl: './bookingList.component.html',
  styleUrls: ['./bookingList.component.scss'],
})
export class bookingListComponent implements OnInit {
  
  bookingUsersList: any[] = [];
  nodata: any = '1';
  
  constructor(
    private UserService: UserService,
    private Router: Router
  ) {

  }

  ngOnInit() {
    this.getBookingList();
  }

//  Get Booking list
  getBookingList()
{
  let xhr = new XMLHttpRequest();
  xhr.open("POST", this.UserService.standardUrl + "/getBookedServicesData");

  xhr.setRequestHeader("Accept", "application/json");
  xhr.setRequestHeader("Content-Type", "application/json");

  xhr.onload = () => this. getBookingListCallback(xhr.responseText)

  let data = {
    "access_token": this.UserService.access_token
  };

  xhr.send(JSON.stringify(data));
}

getBookingListCallback(res: any) {
  res = JSON.parse(res);
 
  if(res.data.length > 0)
  {
   this.nodata = 0;
   this.bookingUsersList =res.data;
  }else{
    this.nodata = 1;
    this.bookingUsersList =[];
  } 
}



}
