import { Component, OnInit } from '@angular/core';
import { UserService } from '../api/user.service';
import { NavigationExtras, Router } from '@angular/router';


@Component({
  selector: 'app-showCredits',
  templateUrl: './showCredits.component.html',
  styleUrls: ['./showCredits.component.scss'],
})
export class showCreditsComponent implements OnInit {
  
  creditList: any[] = [];
  nodata: any = '1';
  
  constructor(
    private UserService: UserService,
    private Router: Router
  ) {

  }

  ngOnInit() {
    this.getCreditList();
  }

//  Get Credit list
getCreditList()
{
  let xhr = new XMLHttpRequest();
  xhr.open("POST", this.UserService.standardUrl + "/getCoachWithCreditRequestsList");

  xhr.setRequestHeader("Accept", "application/json");
  xhr.setRequestHeader("Content-Type", "application/json");

  xhr.onload = () => this. getCreditListCallback(xhr.responseText)

  let data = {
    "access_token": this.UserService.access_token
  };

  xhr.send(JSON.stringify(data));
}

getCreditListCallback(res: any) {
  res = JSON.parse(res);
 
  if(res.data.length > 0)
  {
   this.nodata = 0;
   this.creditList =res.data;
  }else{
    this.nodata = 1;
    this.creditList =[];
  } 
}

gotoDetails(customerNo: any)
{
  let navigationExtras: NavigationExtras = {
    state: {
      data: customerNo
    }
  };
  this.Router.navigate(['/creditDetails'], navigationExtras)
}

}
