import { Component, OnInit } from '@angular/core';
import { DatabaseService } from '../api/database.service';
import { ModalController } from '@ionic/angular';
import { DatepickerPage } from '../datepicker/datepicker.page';
import * as moment from 'moment';
import { PopoverController } from '@ionic/angular';
import { UserService } from '../api/user.service';

@Component({
  selector: 'app-create-advertisement',
  templateUrl: './create-advertisement.component.html',
  styleUrls: ['./create-advertisement.component.scss'],
})
export class CreateAdvertisementComponent implements OnInit {
  startDate: any;
  endDate: any;
  AdvertiseName: any;

  constructor(
    public DatabaseService: DatabaseService,
    private modalController: ModalController,
    private PopoverController: PopoverController,
    public UserService: UserService
  ) { }

  ngOnInit() {}

  handleUpload(event: any) {
    const file = event.target.files[0];
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => {
      console.log(reader.result);
      this.DatabaseService.img = reader.result;
      this.insertImage(reader.result);
      window.open("http://localhost:3000/crop-test.html", '_blank');
    };

  }

  insertImage(img: any) {
    let xhr = new XMLHttpRequest();
    xhr.open("POST", "http://localhost:3000/insert_image");

    xhr.setRequestHeader("Accept", "application/json");
    xhr.setRequestHeader("Content-Type", "application/json");

    xhr.onload = () => this.insertImageCallback(xhr.responseText)

    let data = {
      "base64": img,
      "access_token": this.UserService.access_token
    };

    xhr.send(JSON.stringify(data));
  }

  insertImageCallback(res: any) {
    res = JSON.parse(res);
  }

  async set() {
    if (!this.DatabaseService.img) {
      this.DatabaseService.img = '../../assets/standard.png'
    }
    let result = await this.DatabaseService.createAdvertise(this.AdvertiseName, this.DatabaseService.img, moment(this.startDate).toString(), moment(this.endDate).toString());
    if (result.status == 200) {
      this.UserService.page = 'werbung';
      this.DatabaseService.img = null;
    }
  }

  async showDatepickerFrom() {
    const modal = await this.modalController.create({
      component: DatepickerPage,
      cssClass: 'my-custom-class',
      componentProps: {
        page: 'zeitraumFrom',
        datetime: moment(this.startDate).locale('de').format('ll'),
        presentation: 'date'
      }
    });
    return await modal.present();
  }

  async showDatepickerTo() {
    const modal = await this.modalController.create({
      component: DatepickerPage,
      cssClass: 'my-custom-class',
      componentProps: {
        page: 'zeitraumTo',
        datetime: moment(this.endDate).locale('de').format('ll'),
        presentation: 'date'
      }
    });
    return await modal.present();
  }

}
